<style lang="less">
    @import url("../assets/less/views/home.less");

    * {
        margin: 0;
        padding: 0;
    }

    html, body {
        height: 100%;
    }

    body {
        font-family: "microsoft yahei";
    }

    .swiper-container {
        /*  width: 320px;
          height: 480px;*/
        width: 100%;
        height: 100%;
        background: #000;

    }

    .swiper-slide {
        width: 100%;
        height: 100%;
        background: url(../assets/imgs/bg.jpg) no-repeat left top;
        background-size: 100% 100%;

    }

    img {
        display: block;
    }

    .swiper-pagination-bullet {
        width: 6px;
        height: 6px;
        background: #fff;
        opacity: .4;
    }

    .swiper-pagination-bullet-active {
        opacity: 1;
    }

    @-webkit-keyframes start {
        0%, 30% {
            opacity: 0;
            -webkit-transform: translate(0, 10px);
        }
        60% {
            opacity: 1;
            -webkit-transform: translate(0, 0);
        }
        100% {
            opacity: 0;
            -webkit-transform: translate(0, -8px);
        }
    }

    @-moz-keyframes start {
        0%, 30% {
            opacity: 0;
            -moz-transform: translate(0, 10px);
        }
        60% {
            opacity: 1;
            -moz-transform: translate(0, 0);
        }
        100% {
            opacity: 0;
            -moz-transform: translate(0, -8px);
        }
    }

    @keyframes start {
        0%, 30% {
            opacity: 0;
            transform: translate(0, 10px);
        }
        60% {
            opacity: 1;
            transform: translate(0, 0);
        }
        100% {
            opacity: 0;
            transform: translate(0, -8px);
        }
    }

    .ani {
        position: absolute;
    }

    .txt {
        position: absolute;
    }

    #array {
        position: absolute;
        z-index: 999;
        -webkit-animation: start 1.5s infinite ease-in-out;
    }
</style>
<template>
    <div class="swiper-container">
        <div class="swiper-wrapper ">
            <!-------------slide1----------------->
            <section class="swiper-slide swiper-slide1 page1">
                <div class="ani resize div-text div-title p1"
                     style="left:0px;z-index:3;" swiper-animate-effect="zoomIn"
                     swiper-animate-duration="1s" swiper-animate-delay="0s">这就是要脸的人
                </div>
                <div class="ani resize  div-text div-title p2"
                     style="left:0px;z-index:3;" swiper-animate-effect="fadeIn"
                     swiper-animate-duration="0.5s" swiper-animate-delay="1s">我们100%了解李梦小娟??
                </div>
                <div class="ani resize  div-text div-title p3"
                     style="left:0px;z-index:3;" swiper-animate-effect="fadeIn"
                     swiper-animate-duration="0.5s" swiper-animate-delay="1s">大骗子 拈花惹草 不知羞耻
                </div>
                <div class="ani resize  div-text div-title p4"
                     style="left:0;z-index:2;"
                     swiper-animate-effect="zoomInLeft" swiper-animate-duration="0.5s"
                     swiper-animate-delay="1.5s">
                    <p>&emsp;&emsp;李小娟,办结婚证前一两周,告诉我有病,不要紧的!
                        的确如此,中医、西医都看了个遍,结婚到现在一直都在看病,吃药!
                        我曾经发过誓言,孩子可以不要,我只要她！</p>
                    <p>&emsp;&emsp;竟然在今年正月初八、九(初七就走了),瞒着我和同事出差(下面有与她和她老板的语音),最终被我查到,她和网友出去过夜玩(三天两夜),而且事后几天得了阴道炎。
                    </p>
                    <p>&emsp;&emsp;天天和所谓的网友(虚构22岁 未婚的身份)，视频、语音、电话就算了(口口声称只网上教友，不见面，却天天躲在另一房间视频语音聊天），事后还不要脸的跟我，承认伤了四个网友的心，本人还不算在内！</p>
                    <p>&emsp;&emsp;被我发现后，竟用她的QQ小号谎称是李小娟的网友，说什么她们两男两女去出去玩没什么，而且隐瞒已婚，真TM犀利啊！！！</p>
                </div>
            </section>
            <!---------------slide2-------------->
            <section class="swiper-slide swiper-slide2">
                <div class="ani resize  div-text div-title"
                     style="left:0px;z-index:3;" swiper-animate-effect="fadeIn"
                     swiper-animate-duration="0s" swiper-animate-delay="0s">所谓的出差就是这样的
                </div>
                <div class="div-text div-title">
                    <video id="myvideo" :src="videoSrc" :poster="videoImg" controls="controls"
                           height="50%"
                           width="100%">
                    </video>
                    <div class="ico ico-video" v-on:click="playClick">重播</div>
                    <p style=""
                       class="page1 div-title"> 下面还有</p>
                </div>
            </section>

            <section class="swiper-slide swiper-slide2">
                <div class="ani resize  div-text div-title"
                     style="left:0px;z-index:3;" swiper-animate-effect="fadeIn"
                     swiper-animate-duration="0s" swiper-animate-delay="0s">所谓的出差就是这样的
                </div>
                <div class="div-text div-title">
                    <video id="myvideo1" :src="videoSrc1" :poster="videoImg" controls="controls"
                           height="50%"
                           width="100%">
                    </video>
                    <div class="ico ico-video" v-on:click="playClick1">重播</div>
                    <p style=""
                       class="page1 div-title"> 下面还有</p>
                </div>
            </section>

            <section class="swiper-slide swiper-slide3 page2">
                <div class="ani resize  div-text div-title p1"
                     style="left:0px;z-index:3;" swiper-animate-effect="fadeIn"
                     swiper-animate-duration="0s" swiper-animate-delay="0s">事后不要脸的,通过小号来加我,让我撤销对她的怀疑,下面就是她的微信(微信号就是QQ)
                </div>
                <div class="ani resize  div-title p2"
                     style="left:0px;z-index:3;" swiper-animate-effect="fadeIn"
                     swiper-animate-duration="0s" swiper-animate-delay="0s">
                    <img class="first-img" src="http://os8ntvojr.bkt.clouddn.com/p1.png" style="width: 100%">
                    <p style=""
                       class="page1 div-title"> 下面还有</p>
                </div>
            </section>
            <section class="swiper-slide swiper-slide3 page2">
                <div class="ani resize  div-text div-title p1"
                     style="left:0px;z-index:3;" swiper-animate-effect="fadeIn"
                     swiper-animate-duration="0s" swiper-animate-delay="0s">
                </div>
                <div class="ani resize  div-title p2"
                     style="top: 0px;left:0px;z-index:3;" swiper-animate-effect="fadeIn"
                     swiper-animate-duration="0s" swiper-animate-delay="0s">
                    <img class="first-img" src="http://os8ntvojr.bkt.clouddn.com/13.png" style="width: 100%">
                </div>
            </section>
            <section class="swiper-slide swiper-slide4 page3">
                <div class="ani resize div-text div-title p1"
                     style="left:0px;z-index:3;" swiper-animate-effect="zoomIn"
                     swiper-animate-duration="1s" swiper-animate-delay="0s">还有和她、她老板的通话录音 承认三天两夜是和同事出差！
                </div>
                <div class="ani resize  div-text div-title p2"
                     style="left:0;z-index:2;text-align: left"
                     swiper-animate-effect="zoomInLeft" swiper-animate-duration="0.5s"
                     swiper-animate-delay="1s">
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;试问她(李小娟),我、你同事、家人让你骗我们,整整三天出差?我让你和网友聊得火热,还出去过夜见面玩?</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;你同事允许你，以同事身份出来和你不只是玩还牵手!?你爸妈教你，教你婚后可以胡乱和网友出来浪?</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;你爷爷教你，在我面前提伤了四个网友心?你姐教你难怀孕有病就是有出去浪 撩网友的借口?</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;有病吧,我陪你治了两年;网上教友吧,我信任你,你给我的就这结果?!</p>
                </div>
            </section>

        </div>
        <p style="position: absolute;z-index: 999;bottom:15px; text-align: center;margin-top: -17px;color: #ddd;width: 100%;font-size: 16px"
           class="page1 div-title"> 上拉翻页(视频语音图片)</p>
        <img src="../assets/imgs/arrow.png" style="  " id="array"
             class="resize bottom">
        <div class="swiper-pagination">
        </div>
    </div>
    <!--video id="myvideo" :src="videoSrc" :poster="videoImg"  controls="controls" :muted="muteStatus" height="414"
           width="100%">
        your browser does not support the video tag
    </video>
    <span class="ico ico-sound" :class="{ active: isMute }" v-on:click="closeSoundClick">22</span>
    <span class="ico ico-skip">33</span>
    <span class="ico ico-video" :class="{ hide: isPlay }" v-on:click="playClick">11</span-->

</template>
<script>
    import 'videojs-contrib-hls'

    let scaleW = window.innerWidth / 320;
    let scaleH = window.innerHeight / 480;
    var resizes = document.querySelectorAll('.resize');
    for (var j = 0; j < resizes.length; j++) {
        resizes[j].style.width = parseInt(resizes[j].style.width) * scaleW + 'px';
        resizes[j].style.height = parseInt(resizes[j].style.height) * scaleH + 'px';
        resizes[j].style.top = parseInt(resizes[j].style.top) * scaleH + 'px';
        resizes[j].style.left = parseInt(resizes[j].style.left) * scaleW + 'px';
    }
    export default {
        name: "home",
        components: {},
        data () {
            return {
                _dom: "",
                videoSrc: 'http://os8ntvojr.bkt.clouddn.com/qianshou.MP4',
                videoSrc1: 'http://os8ntvojr.bkt.clouddn.com/qianshou2.MP4',
                videoImg: 'http://os8ntvojr.bkt.clouddn.com/v.png',
                playStatus: '',
                muteStatus: '', isMute: true,
                isPlay: false,
                scaleW: window.innerWidth / 320,
                scaleH: window.innerHeight / 480,
                resizes: [],
                mySwiper: {},
            };
        },
        methods: {
            playClick () {
                this._dom = document.getElementById('myvideo');
                this.isPlay = !this.isPlay;
                this._dom.play();
            },
            playClick1 () {
                this._dom = document.getElementById('myvideo1');
                this.isPlay = !this.isPlay;
                this._dom.play();
            },
            closeSoundClick () {
                this.isMute = !this.isMute;
                if (this.isMute) {
                    this.muteStatus = '';
                } else {
                    this.muteStatus = 'muted';
                }
            }
        },
        mounted: {},
        activated () {
            init();
        }

    };

    function init () {
        var mySwiper = new Swiper('.swiper-container', {
            direction: 'vertical',
            pagination: '.swiper-pagination',
            //virtualTranslate : true,
            mousewheelControl: true,
            onInit: function (swiper) {
                swiperAnimateCache(swiper);
                swiperAnimate(swiper);
            },
            onSlideChangeEnd: function (swiper) {
                swiperAnimate(swiper);
            },
            onTransitionEnd: function (swiper) {
                swiperAnimate(swiper);
            },


            watchSlidesProgress: true,

            onProgress: function (swiper) {
                for (var i = 0; i < swiper.slides.length; i++) {
                    var slide = swiper.slides[i];
                    var progress = slide.progress;
                    var translate = progress * swiper.height / 4;
                    var scale = 1 - Math.min(Math.abs(progress * 0.5), 1);
                    var opacity = 1 - Math.min(Math.abs(progress / 2), 0.5);
                    slide.style.opacity = opacity;
                    var es = slide.style;
                    es.webkitTransform = es.MsTransform = es.msTransform = es.MozTransform = es.OTransform = es.transform = 'translate3d(0,' + translate + 'px,-' + translate + 'px) scaleY(' + scale + ')';

                }
            },

            onSetTransition: function (swiper, speed) {
                for (var i = 0; i < swiper.slides.length; i++) {
                    var es = swiper.slides[i].style;
                    es.webkitTransitionDuration = es.MsTransitionDuration = es.msTransitionDuration = es.MozTransitionDuration = es.OTransitionDuration = es.transitionDuration = speed + 'ms';

                }
            },
        })
    }
</script>




